<template>
  <div>
    A组件:
    <span>{{elementValue}}</span>
    <input type="button" value="点击触发" @click="elementByValue(1)">
    <input type="button" value="点击触发" @click="elementByValue(2)">
    <input type="button" value="点击触发" @click="elementByValue(3)">
  </div>
</template>
<script>
/* eslint-disable */
// 引入公共的bug，来做为中间传达的工具
import Bus from './bus'
export default {
  name: 'aaa',
  data() {
    return {
      elementValue: 0
    }
  },
  methods: {
    elementByValue: function (x) {
      Bus.$emit('elementByValue', x)
    }
  }
}
</script>


