<template>
  <div>
    父组件:
    <input type="text" v-model="name">
    <br>
    <br>
    <!-- 引入子组件 -->
    <child :inputName="name"></child>
  </div>
</template>
<script>
/* eslint-disable */
import child from './child'
export default {
  name: 'parent',
  components: {
    child
  },
  data() {
    return {
      name: ''
    }
  }
}
</script>


