
<template>
    <div>
        <input type="text" v-model="num">
        <template v-if="num==1">
            <h3>今天天气不错</h3>
        </template>
        <template v-else-if="num==2">
            <h3>今天天气不错2</h3>
        </template>
        <template v-else-if="num==3">
            <h3>今天天气不错3</h3>
        </template>
    </div>
</template>
<script>
/* eslint-disable */
export default {
    name: 'P7',
    data() {
        return {
            num: 2
        }
    }
}
</script>

<style scoped>
div {
  margin: 20px 8px;
}
</style>




