
<template>
    <div>
        <div :class="[blue2]">{{str}}</div>
        <div :style="{color:red2}">{{str}}</div>
        <div>
            <button @click="change()">改变</button>
            <button @click="red2='red';blue2='blue'">还原</button>
        </div>
    </div>
</template>
<script>
/* eslint-disable */ 
    export default {
        name: 'P8',
        data() {
            return {
                str: '今天天气不错',
                red2: 'red',
                blue2: 'blue'
            }
        },
        methods: {
            change: function(e) {
                this.red2 = 'blue',
                    this.blue2 = 'red'
            }
        }
    }
</script>

<style scoped>
    div {
        margin: 20px 8px;
    }
    .red {
        color: yellow;
    }
    .blue {
        color: blue;
    }
</style>




