
<template>
    <div>
       <input type="text" v-abc="{color:'red',bg:'blue'}" v-model="v" />
       <input type="text" v-def v-model="v"/>
    </div>
</template>

<script>
/* eslint-disable */
export default {
    name: 'P13',
    data() {
        return {
           v:'abc'
        }
    },
    methods: {

    },
    directives: {
        'abc': function (el, binding) {
            el.style.color = binding.value.color;
            el.style.backgroundColor = binding.value.bg;

        },
        'def': {
            inserted: function (el) {
                el.focus();
            }
        }
    }
}
</script>

<style scoped>
div {
  margin: 20px 8px;
}
.red {
  color: red;
}
.blue {
  color: blue;
}
ul {
  width: 500px;
}
ul li:first-child {
  font-weight: bold;
}
ul li {
  border-bottom: solid 1px #ccc;
  padding: 5px 0px;
}
ul li span {
  width: 80px;
  display: inline-block;
}
</style>




