<template>
    <div>
        <nav :class="active">
            <a href="#/nav" class="a1" @mousemove="move('a1')">首页</a>
            <a href="#/nav" class="a2" @mousemove="move('a2')">新闻</a>
            <a href="#/nav" class="a3" @mousemove="move('a3')">图片</a>
        </nav>
        <div style="clear: left"></div>
        <div v-show="active=='a1'" class="content">
            这是首页面
        </div>
        <div v-show="active=='a2'" class="content">
            这是新闻页
        </div>
        <div v-show="active=='a3'" class="content">
            这是图片页
        </div>
    </div>
</template>
<script>
export default {
/* eslint-disable */
    name: 'nav2',
    data() {
        return {
            active: 'a1'
        }
    },
    methods: {
        move: function (v) {
            this.active = v
        }
    }
}
</script>
<style scoped>
nav a {
  float: left;
  width: 80px;
  text-align: center;
  padding: 10px 0px;
}
nav.a1 .a1,
nav.a2 .a2,
nav.a3 .a3 {
  background-color: red;
  color: #fff;
}
#app .content {
  margin: 20px 0px;
}
</style>





