<template>
  <div>
    B组件:
    <span>{{name}}</span>
    <aaa />
  </div>
</template>
<script>
/* eslint-disable */
import Bus from './bus'
import aaa from './aaa'
export default {
  name: 'bbb',
  data() {
    return {
      name: 0
    }
  },
  components:{
     aaa
  },
  mounted: function () {
    var vm = this
    // 用$on事件来接收参数
    Bus.$on('elementByValue', (data) => {
      console.log(data)
      vm.name = data
    })
  }
}
</script>


