/* this.$route是一个数组，里面包含路由的所有信息
使用：this.$route.params.cid 需要配置路由 */
<template>
    <div>
    <router-link :to="{path:'/test',query: {id: 123}}">跳转</router-link>
    <router-link :to="'/test/456'">跳转</router-link>
    </div>
</template>
<script>
/* eslint-disable */
export default {
    name: 'ccc',
    methods: {

    }
}
</script>




