
<template>
    <div>
       <my-input :num="3" tip="请输入一个大于5的数字" />
    </div>
</template>

<script>
/* eslint-disable */
export default {
    name: 'P11',
    data() {
        return {

        }
    },
    components: {
        myInput: {
            props: {
                'num': {
                    type: Number,
                    required: true,
                    validator: function (v) {
                        return v > 5
                    }
                },
                'tip': {
                    type: String,
                    required: true
                }
            },
            template:
                '<div><input type="text" :value="num" /><div>{{tip}}</div></div>'
        }
    }
}
</script>

<style scoped>
div {
  margin: 20px 8px;
}
.red {
  color: red;
}
.blue {
  color: blue;
}
ul {
  width: 500px;
}
ul li:first-child {
  font-weight: bold;
}
ul li {
  border-bottom: solid 1px #ccc;
  padding: 5px 0px;
}
ul li span {
  width: 80px;
  display: inline-block;
}
</style>




