<template>
   <div>
    <div>接收到的值是:{{id}}{{cid}}</div>
   <button @click="getV()">获取</button>
   <button @click="getV2()">获取2</button>
   </div>
</template>
<script>
/* eslint-disable */
export default {
    name: 'test',
    data() {
        return {
            id: 0,
            cid: 0
        }
    },
    methods: {
        getV: function () {
            this.id = this.$route.query.id
        },
        getV2: function () {
            this.cid = this.$route.params.cid
        }
    }
}
</script>



