
<template>
    <div>
        <div>输入查询的姓名：<input type="text" v-model="key"></div>
        <ul>
            <li>
                <span @click="isAsc('name',false)">姓名</span>
                <span @click="isAsc('sex',false)">性别</span>
                <span @click="isAsc('score',false)">分数</span>
            </li>
            <li :key="item.id" v-for="item in searchData">
                <span>{{item.name}}</span>
                <span>{{item.sex}}</span>
                <span>{{item.score}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
/* eslint-disable */ 
    export default {
        name: 'P5',
        data() {
            return {
                key: '',
                stu: [{
                        id: 1010,
                        name: '张小明',
                        sex: '男',
                        score: 60
                    },
                    {
                        id: 1011,
                        name: '李松年',
                        sex: '女',
                        score: 30
                    },
                    {
                        id: 1012,
                        name: '陈百华',
                        sex: '男',
                        score: 75
                    },
                    {
                        id: 1013,
                        name: '张明明',
                        sex: '女',
                        score: 50
                    },
                    {
                        id: 1014,
                        name: '刘思思',
                        sex: '女',
                        score: 90
                    },
                    {
                        id: 1015,
                        name: '宗小田',
                        sex: '女',
                        score: 95
                    }
                ]
            }
        },
        computed: {
            searchData: function() {
                var key = this.key;
                if (key) {
                    return this.stu.filter(function(stu) {
                        return stu.name.indexOf(key) != -1;
                    })
                }
                //初始化数据
                return this.stu;
            }
        },
        methods: {
            sortExp: function(key, isAsc) {
                this.bln = !this.btn;
                return function(x, y) {
                    if (isNaN(x[key])) {
                        if (x[key] > y[key]) return 1 * (isAsc ? 1 : -1);
                        if (x[key] < y[key]) return -1 * (isAsc ? 1 : -1);
                        return 0;
                    } else {
                        return parseInt(x[key]) - parseInt(y[key]);
                    }
                }
            },
            isAsc: function(key, isAsc) {
                this.searchData.sort(this.sortExp(key, isAsc));
            }
        }
    }
</script>

<style scoped>
    div {
        margin: 20px 8px;
    }
    ul {
        width: 500px
    }
    ul li:first-child {
        font-weight: bold;
    }
    ul li {
        border-bottom: solid 1px #ccc;
        padding: 5px 0px;
    }
    ul li span {
        width: 80px;
        display: inline-block;
    }
</style>




