
<template>
    <div>
        <ul>
            <li :key="item.id" v-for="item in stu">
                <span>{{item.name}}</span>
                <span>{{item.sex}}</span>
                <span>{{item.score}}</span>
            </li>
        </ul>
        <div>
            姓名：<input type="text" v-model="name"><br> 性别：
            <select name="sex" v-model="selected">
                        <option :value="sex[item]" :key="index"  v-for="(index,item) in sex">{{sex[item]}}</option>
                    </select><br> 分数：
            <input type="number" v-model="score"><br>
            <button @click="add()">增加</button>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
    export default {
        name: 'P4',
        data() {
            return {
                selected: '男',
                name: '张三',
                sex: ['男', '女'],
                score: 30,
                stu: [{
                        id: 1010,
                        name: '张小明',
                        sex: '男',
                        score: 60
                    },
                    {
                        id: 1011,
                        name: '李松年',
                        sex: '男',
                        score: 70
                    },
                    {
                        id: 1012,
                        name: '陈百华',
                        sex: '男',
                        score: 75
                    },
                    {
                        id: 1013,
                        name: '张明明',
                        sex: '女',
                        score: 80
                    },
                    {
                        id: 1014,
                        name: '刘思思',
                        sex: '女',
                        score: 90
                    },
                    {
                        id: 1015,
                        name: '宗小田',
                        sex: '女',
                        score: 95
                    }
                ]
            }
        },
        methods: {
            add: function(e) {
                console.log(this.name + "/" + this.selected + "/" + this.score)
                var _obj = {
                    id: this.stu.length + 1,
                    name: this.name,
                    sex: this.selected,
                    score: this.score
                }
                this.stu.push(_obj);
            }
        }
    }
</script>

<style scoped>
    div {
        margin: 20px 8px;
    }
    ul {
        width: 500px
    }
    ul li {
        border-bottom: solid 1px #ccc;
        padding: 5px 0px;
    }
    ul li span {
        width: 80px;
        display: inline-block;
    }
</style>




