
<template>
  <div>
    <div>输入姓名：<input type="text" v-model="str"></div>
    <div>{{str|reverse}}</div>
    <div v-bind:title="str|reverse">{{str|reverse}}</div>
  </div>
</template>

<script>
/* eslint-disable */ 
  export default {
    name: 'P6',
    data() {
      return {
        str: ''
      }
    },
    filters: {
      reverse: function(v) {
        return v.split('').reverse().join('');
      }
    }
  }
</script>

<style scoped>
  div {
    margin: 20px 8px;
  }
</style>




