<template>
    <div>
        <ul>
            <li :key="item.id" v-for="item in items">
                <img :src="item.img" alt="" class="img">
                <div>
                    {{item.title}} 定义：{{item.price}}
                </div>
            </li>
        </ul>
        <div>{{tip}}</div>
        <button @click="ask()">请求数据</button>
        <button @click="ask2()">单一数据</button>
    </div>
</template>

<script>
/* eslint-disable */
export default {
    name: 'list',
    data() {
        return {
            items: [],
            tip: ''
        }
    },
    methods: {
        ask: function (e) {
            this.$http.get('/books').then((data) => {
                // 由于请求成功返回的是Promise对象，我们要从data.data.data拿到books数组  
                this.items = data.data.data;
            })
        },
        ask2: function (e) {
            this.$http.get('http://rttop.cn/api/?day=1-1').then((data) => {
                // 由于请求成功返回的是Promise对象，我们要从data.data.data拿到books数组  
                this.tip = data.data;
            })
        }
    }
}
</script>

<style scoped>
ul {
  list-style: none;
  padding: 0px;
  margin: 0px;
}
ul li {
  float: left;
}
.img {
  width: 100px;
  padding: 8px;
  border: solid 1px #ccc;
}
</style>





