
<template>
    <div>
        <div>{{a1}}</div>
        <my-buttton />
        <my-buttton @add="add()" />
    </div>
</template>

<script>
/* eslint-disable */
export default {
    name: 'P12',
    data() {
        return {
            a1: 1
        }
    },
    methods: {
        add: function () {
            this.a1 += 2
        }
    },
    components: {
        myButtton: {
            template: '<div><button @click="add">{{a}}</button></div>',
            data() {
                return {
                    a: 0
                }
            },
            methods: {
                add: function (e) {
                    this.a += 1;
                    this.$emit('add')
                }
            }
        }
    }
}
</script>

<style scoped>
div {
  margin: 20px 8px;
}
.red {
  color: red;
}
.blue {
  color: blue;
}
ul {
  width: 500px;
}
ul li:first-child {
  font-weight: bold;
}
ul li {
  border-bottom: solid 1px #ccc;
  padding: 5px 0px;
}
ul li span {
  width: 80px;
  display: inline-block;
}
</style>




