
<template>
    <div>
        <title2 val="学生信息表" />
        <ul>
            <list :key="item.id" v-for="item in stu" :a="item.name" :b="item.sex" :c="item.score" />
        </ul>
    </div>
</template>

<script>
/* eslint-disable */
export default {
    name: 'P10',
    data() {
        return {
            stu: [{
                id: 1010,
                name: '张小明',
                sex: '男',
                score: 60
            },
            {
                id: 1011,
                name: '李松年',
                sex: '男',
                score: 70
            },
            {
                id: 1012,
                name: '陈百华',
                sex: '男',
                score: 75
            },
            {
                id: 1013,
                name: '张明明',
                sex: '女',
                score: 80
            },
            {
                id: 1014,
                name: '刘思思',
                sex: '女',
                score: 90
            },
            {
                id: 1015,
                name: '宗小田',
                sex: '女',
                score: 95
            }
            ]
        }
    },
    components: {
        title2: {
            props: ['val'],
            template: '<h3>{{val}}</h3>'
        },
        list: {
            props: ['a', 'b', 'c'],
            template: '<li><span>{{a}}</span><span>{{b}}</span><span>{{c}}</span></li>'
        }
    }
}
</script>

<style scoped>
div {
  margin: 20px 8px;
}
.red {
  color: red;
}
.blue {
  color: blue;
}
ul {
  width: 500px;
}
ul li:first-child {
  font-weight: bold;
}
ul li {
  border-bottom: solid 1px #ccc;
  padding: 5px 0px;
}
ul li span {
  width: 80px;
  display: inline-block;
}
</style>




