<template>
    <div>
     <div>
         <span :class="cur==1?'act':''" @click="tab(1)">新闻</span>
         <span :class="cur==2?'act':''" @click="tab(2)">图片</span>
         <span :class="cur==3?'act':''" @click="tab(3)">热点</span>
     </div>
     <div v-show="cur==1">新闻</div>
     <div v-show="cur==2">图片</div>
     <div v-show="cur==3">热点</div>
    </div>
</template>
<script>
/* eslint-disable */
export default {
    name:"tab",
    data(){
        return{
           cur:1
        }
    },
    methods: {
     
       tab:function(index){
           this.cur=index;
       }

    }
}
</script>
<style scoped>
     .act{
         background-color: red;
     }
</style>





