/* 可以看出computed计算属性的话，每次进入页面将一直沿用第一次的信息，不会再触发now，这就是依赖缓存。
而methods是实时的，在重新渲染时，函数总会重新调用执行，不会缓存，
可以说使用 computed 性能会更好，但是如果你不希望缓存，你可以使用 methods 属性。 */
<template>
    <div>
        <div class="test">    <!--computed计算属性-->
            <p>{{now3}}</p>
            <p>{{now3}}</p>
            <p>{{now3}}</p>
            <p>{{now3}}</p>
            <hr />            <!--横线分割-->
        </div>
        <div class="test2">   <!--methods方法，注意new（）加了括号-->
            <p>{{now2()}}</p>
            <p>{{now2()}}</p>
            <p>{{now2()}}</p>
            <p>{{now2()}}</p>
        </div>
    </div>
</template>
<script>
export default {
/* eslint-disable */
    name: 'm2',
    data() {
       return{

       }
    },
    computed: {
        now3: function () {
            var yanshi = 0;
            for (var o = 0; o < 2000; o++) {     //延时
                for (var q = 0; q < 2000; q++) {
                    yanshi++;
                }
            }
            return Date.now()
        }
    },
    methods: {
        now2: function () {
            var yanshi = 0;
            for (var o = 0; o < 2000; o++) {
                for (var q = 0; q < 2000; q++) {
                    yanshi++;
                }
            }
            return Date.now()
        }
    }
}
</script>



