import Vue from 'vue'
import Router from 'vue-router'
import P1 from '@/components/第一个测试页1'
import P2 from '@/components/数据的绑定2'
import P3 from '@/components/事件的绑定3'
import P4 from '@/components/列表的绑定4'
import P5 from '@/components/列表的过滤5'
import P6 from '@/components/管道符的使用6'
import P7 from '@/components/条件模板7'
import P8 from '@/components/样式的动态绑定8'
import P9 from '@/components/组件定义和传值9'
import P10 from '@/components/组件的动态传值10'
import P11 from '@/components/组件属性值的验证11'
import P12 from '@/components/组件中的事件12'
import P13 from '@/components/自定义指令13'
import nav2 from '@/components/案例-导航条1'
import m2 from '@/components/案例-方法差异2'
import parent from '@/components/组件间传值父传子/parent'
import parent2 from '@/components/组件间传值子传父/parent'
import aaa from '@/components/非父子组件间传值/aaa'
import bbb from '@/components/非父子组件间传值/bbb'
import ccc from '@/components/路由传参/ccc'
import test from '@/components/路由传参/test'
import list from '@/components/获取接口数据/list'
import base3 from '@/components/echarts组件/基础应用'
import base2 from '@/components/echarts组件/关联提示'
import demo from '@/components/样式插件/demo'
import tab from '@/components/tab'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'P1',
      component: P1
    },
    {
      path: '/t',
      name: 'tab',
      component: tab
    },
    {
      path: '/p2',
      name: 'P2',
      component: P2
    },
    {
      path: '/p3',
      name: 'P3',
      component: P3
    },
    {
      path: '/p4',
      name: 'P4',
      component: P4
    },
    {
      path: '/p5',
      name: 'P5',
      component: P5
    },
    {
      path: '/p6',
      name: 'P6',
      component: P6
    },
    {
      path: '/p7',
      name: 'P7',
      component: P7
    },
    {
      path: '/p8',
      name: 'P8',
      component: P8
    },
    {
      path: '/p9',
      name: 'P9',
      component: P9
    },
    {
      path: '/p10',
      name: 'P10',
      component: P10
    },
    {
      path: '/p11',
      name: 'P11',
      component: P11
    },
    {
      path: '/p12',
      name: 'P12',
      component: P12
    },
    {
      path: '/p13',
      name: 'P13',
      component: P13
    },
    {
      path: '/nav2',
      name: 'nav2',
      component: nav2
    },
    {
      path: '/m2',
      name: 'm2',
      component: m2
    },
    {
      path: '/parent',
      name: 'parent',
      component: parent
    },
    {
      path: '/parent2',
      name: 'parent',
      component: parent2
    },
    {
      path: '/b',
      name: 'bbb',
      component: bbb
    },
    {
      path: '/a',
      name: 'aaa',
      component: aaa
    },
    {
      path: '/c',
      name: 'ccc',
      component: ccc
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/test/:cid',
      name: 'test',
      component: test
    },
    {
      path: '/list',
      name: 'list',
      component: list
    },
    {
      path: '/base',
      name: 'base',
      component: base3
    },
    {
      path: '/base2',
      name: 'base2',
      component: base2
    },
    {
      path: '/demo',
      name: 'demo',
      component: demo
    }
  ]
})



