<template>
    <div>
        <p v-show="bln">{{str}}</p>
        <button @click="aa()">显示与隐藏</button>
    </div>
</template>

<script>
/* eslint-disable */
export default {
    name: 'P3',
    data() {
        return {
            bln: true,
            str: '明天的天气会更好'
        }
    },
    methods: {
        aa: function (e) {
            this.bln = !this.bln;
        }
    }
}
</script>

<style scoped>
div {
  margin: 20px 8px;
}
</style>




