<template>
    <div>
        <div>{{bln?'显示':'隐藏'}}</div>
        <div :class="'c'+id">{{str}}</div>
        <div v-html="str2"></div>
    </div>
</template>

<script>
/* eslint-disable */ 
    export default {
        name: 'P2',
        data() {
            return {
                bln: true,
                str: '今天的天气不错',
                str2: '<b>今天的天气不错</b>',
                id: 1
            }
        }
    }
</script>

<style scoped>
    div {
        margin: 20px 8px;
    }
    .c1 {
        color: blue;
    }
</style>




